<template>
  <div id="user-box">
    <!--  -->
    <ul class="title no-fa">
      <li class="widget-title" v-for="(item,index) in message" :key="index" @mouseover="currentIndex = index">
        <a :class="{'title-active':currentIndex == index}">{{item.name}}</a>
      </li>
    </ul>
    <!--  -->
    <div class="widget-content-box">
      <!--   -->
      <div v-show="currentIndex==0">
        <keep-alive>
          <user-login @getSubscriptionData='getSubscriptionData()'></user-login>
        </keep-alive>
      </div>
      <!--   -->
      <div v-show="currentIndex!=0">
        <div class="normal-list">
          <ul>
            <li class="contentLi" v-for="(item,index) in message[currentIndex].message" v-if="index<8" :key="index">
              <a :title="item.title" class="contentNoTimeTitle" @click.stop="query.message=Object.assign({},message[currentIndex]),query.id=item.id,routerLink('content')">{{item.title}}</a>
            </li>
          </ul>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import UserLogin from "@/components/common/tools/user-login";
import { getSubscribe } from "@/api/user";
export default {
  name: "userBox",
  data() {
    return {
      message: [
        {
          name: "",
          belong: "" //
        },
        {
          name: "",
          belong: "", //
          message: null //
        },
        {
          name: "",
          belong: "", //
          message: null //
        }
      ],
      currentIndex: 0,
      query: {}
    };
  },
  watch: {
    currentIndex: {
      //
      handler(val) {
        //
      },
      immediate: true //watch
    }
  },

  methods: {
    //
    routerLink: function(childrenPath) {
      if (this.query.id) {
        // let path =
        //   this.message[this.currentIndex].path +
        //   this.message[this.currentIndex].path +
        //   "_" +
        //   childrenPath;
        let path = childrenPath;
        delete this.query.message.message; //
        let data = JSON.stringify(this.query); ///JSON[Object,Object]
        this.$router.push({ path: path, query: { queryMsg: data } });
      }
    },
    //
    getSubscriptionData(i) {
      i = i || global.tabs.findIndex(item => item.name == "");
      getSubscribe(this.HOST)
        .then(data => {
          if (data.message)
            this.message[1].message = global.tabs[i].message = data.message;
        })
        .catch(err => {
          new Error(err);
        });
    }
  },

  mounted() {
    let i = global.tabs.findIndex(item => item.name == "");
    this.message[1] = global.tabs[i];
    if (!global.tabs[i].message) this.getSubscriptionData(i);
    this.message[2].message = [
      {
        title: "KR 1000 titan F",
        time: "2018-09-20"
      },
      {
        title: "KR 470-2 PA",
        time: "2018-09-20"
      },
      {
        title: "KR 3R540",
        time: "2018-09-20"
      },
      {
        title: "KR 600 R2830 F",
        time: "2018-09-20"
      },
      {
        title: "",
        time: "2018-09-20"
      },
      {
        title: "",
        time: "2018-09-20"
      },
      {
        title: "",
        time: "2018-09-20"
      },
      {
        title: "",
        time: "2018-09-20"
      }
    ];
  },
  components: {
    UserLogin
  }
};
</script>

<style lang="scss" scoped>
.title {
  margin-bottom: 10px;
  margin-top: 2px;
  width: 100%;
  background: #fff;
  height: 32px;
  line-height: 28px;
}
.title li {
  float: left;
}
.widget-title a {
  cursor: pointer;
  color: #7b7b7b;
  font-size: 1em;
  font-weight: 500;
  line-height: 25px;
  vertical-align: middle;
  margin-right: 10px;
}
.widget-content-box > div:first-child {
  margin-top: -3px;
}
</style>



